import React, { useContext, useMemo } from 'react';
import { Accordion, AccordionDetails, AccordionSummary, Stack, Typography } from '@mui/material';
import { getRealDateInMs, prefix } from 'utility/helpers';
import { AppContext } from 'components/common/context/AppProvider';
import styled from '@emotion/styled';
import Timer from 'components/common/Timer';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { calcHappyHours } from '../../../parsers/dungeons';
import { NextSeo } from 'next-seo';
import InfoIcon from '@mui/icons-material/Info';
import Tooltip from '../../../components/Tooltip';
import { CardTitleAndValue } from '@components/common/styles';
import Upgrades from '../../../components/account/Misc/Dungeons/Upgrades';
import Tabber from '../../../components/common/Tabber';
import RngItems from '../../../components/account/Misc/Dungeons/RngItems';
import Traits from '../../../components/account/Misc/Dungeons/Traits';
const Dungeons = () => {
  const {
    state
  } = useContext(AppContext);
  const {
    dungeons
  } = state?.account || {};
  const nextHappyHours = useMemo(() => calcHappyHours(state?.serverVars?.HappyHours) || [], [state]);
  return <>
      <NextSeo title="Dungeons | Idleon Toolbox" description="Dungeon information including happy hour times, currency, upgrades and more" data-sentry-element="NextSeo" data-sentry-source-file="dungeons.jsx" />
      <Stack direction="row" flexWrap={'wrap'} gap={4} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
        <CardTitleAndValue title={'Rank'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="dungeons.jsx">
          <Stack direction={'row'} gap={2} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
            <CurrencyIcon src={`${prefix}data/Dung_Rank${dungeons?.rank}.png`} alt="" data-sentry-element="CurrencyIcon" data-sentry-source-file="dungeons.jsx" />
            <Stack data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
              <Typography data-sentry-element="Typography" data-sentry-source-file="dungeons.jsx">Rank: {dungeons?.rank}</Typography>
              <Typography data-sentry-element="Typography" data-sentry-source-file="dungeons.jsx">Exp: {dungeons?.progress} / {dungeons?.rankReq}</Typography>
            </Stack>
          </Stack>
        </CardTitleAndValue>
        <CardTitleAndValue title={'Boosted Runs'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="dungeons.jsx">
          <Stack direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
            <img src={`${prefix}etc/boosted-runs.png`} alt="" />
            <Typography data-sentry-element="Typography" data-sentry-source-file="dungeons.jsx">{dungeons?.boostedRuns}</Typography>
          </Stack>
        </CardTitleAndValue>
        <CardTitleAndValue title={'Credits'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="dungeons.jsx">
          <Stack direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
            <CurrencyIcon src={`${prefix}data/DungCredits1.png`} alt="" data-sentry-element="CurrencyIcon" data-sentry-source-file="dungeons.jsx" />
            {dungeons?.credits}
          </Stack>
        </CardTitleAndValue>
        <CardTitleAndValue title={'Flurbos'} data-sentry-element="CardTitleAndValue" data-sentry-source-file="dungeons.jsx">
          <Stack direction={'row'} gap={1} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
            <CurrencyIcon src={`${prefix}data/DungCredits2.png`} alt="" data-sentry-element="CurrencyIcon" data-sentry-source-file="dungeons.jsx" />
            {dungeons?.flurbos}
          </Stack>
        </CardTitleAndValue>
      </Stack>

      <Stack my={2} direction="row" gap={4} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
        <Accordion data-sentry-element="Accordion" data-sentry-source-file="dungeons.jsx">
          <AccordionSummary expandIcon={nextHappyHours.length > 1 ? <ExpandMoreIcon /> : null} data-sentry-element="AccordionSummary" data-sentry-source-file="dungeons.jsx">
            <Stack direction="row" gap={2} data-sentry-element="Stack" data-sentry-source-file="dungeons.jsx">
              <Typography data-sentry-element="Typography" data-sentry-source-file="dungeons.jsx">Next Happy Hour:</Typography>
              {nextHappyHours?.length > 0 ? <>
                  <Timer type={'countdown'} date={nextHappyHours?.[0]} lastUpdated={state?.lastUpdated} />
                  <Tooltip title={getRealDateInMs(nextHappyHours?.[0])}>
                    <InfoIcon fontSize={'small'} />
                  </Tooltip>
                </> : 'waiting for lava to set them'}
            </Stack>
          </AccordionSummary>
          {nextHappyHours.length > 1 ? <AccordionDetails>
              <Typography mb={1}>Future happy hours</Typography>
              <Stack gap={2}>
                {nextHappyHours.map((nextHappyHour, index) => {
              if (index === 0) return null;
              return <Stack key={`next-happy-hour-${index}`} direction={'row'} gap={3}>
                    <Typography sx={{
                  width: 40
                }}>#{index}</Typography>
                    <Timer key={`happy-${index}`} date={nextHappyHour} lastUpdated={state?.lastUpdated} />
                    <Tooltip title={getRealDateInMs(nextHappyHour)}>
                      <InfoIcon fontSize={'small'} />
                    </Tooltip>
                  </Stack>;
            })}
              </Stack>
            </AccordionDetails> : null}
        </Accordion>
      </Stack>
      <Tabber tabs={['Passives', 'Rng Items', 'Traits']} data-sentry-element="Tabber" data-sentry-source-file="dungeons.jsx">
        <Upgrades {...dungeons} data-sentry-element="Upgrades" data-sentry-source-file="dungeons.jsx" />
        <RngItems {...dungeons} data-sentry-element="RngItems" data-sentry-source-file="dungeons.jsx" />
        <Traits {...dungeons} data-sentry-element="Traits" data-sentry-source-file="dungeons.jsx" />
      </Tabber>
    </>;
};
const CurrencyIcon = styled.img`
  width: 25px;
  object-fit: contain;
`;
export default Dungeons;